import React, { Component } from 'react';
import Nav from './Nav';
import ProjectContainer from './projects/ProjectContainer';
// import { ParallaxProvider } from 'react-scroll-parallax';

export default class Crousel extends Component {
	constructor() {
		super();
		this.state = {
			fullMode: false,
			imgTop: 50,
			headTop: 50,
			next: 0,
			removeTransition: false,
			data : [1, 's p e c t r u m','Monitor Aws services',require("../assets/images/spectrum-cover.png"), '#24C9AF'],
			alldata : [
				[1, 'm e d t r a i l','Patients Medical History',require("../assets/images/medtrail-cover.png"), '#E77060'],
				[2, 's p e c t r u m','Monitor Aws services',require("../assets/images/spectrum-cover.png"), '#24C9AF'],
				[3, 'w o o b l y','Experience Delightful Hangouts',require("../assets/images/woobly-cover.png"), '#552884'],
				[4, 'e x c u s e r','Excape Boredome',require("../assets/images/excuser-cover.png"), '#44C3E5'],
			]
		}

		this.setFull = this.setFull.bind(this);
		this.removeFull = this.removeFull.bind(this);
		this.scrollTrans = this.scrollTrans.bind(this);
		this.next = this.next.bind(this);
		this.prev = this.prev.bind(this);
	}
	componentWillReceiveProps(nextProps) {
	    this.setState({ data: this.state.alldata[this.props.crouselNum] });
	}
	componentDidMount() {
		this.setState({data: this.state.alldata[this.props.crouselNum]});
		window.addEventListener('scroll', () => {
			this.scrollTrans();
		})
	}
	componentWillUnmount() {
		this.setState({data: []});
		window.removeEventListener('scroll', () => {
			this.scrollTrans();
		})
	}
	setFull() {
		this.setState({fullMode: true});
		setTimeout(function() { this.setState({removeTransition: true})}.bind(this),400);
	}
	removeFull() {
		this.setState({removeTransition: false});
		setTimeout(function() { this.setState({fullMode: false})}.bind(this),400);
	}
	scrollTrans() {
		this.setState({imgTop: (50 - window.scrollY/15)});
		this.setState({headTop: (50 - window.scrollY/10)});
	}
	next() {
		this.setState({next: 1});
		this.setState({removeTransition: true});
		setTimeout(function() { this.setState({next: 2}); }.bind(this), 400);
		setTimeout(function() { this.setState({next: 3}); }.bind(this), 800);
		setTimeout(function() { this.setState({next: 4}); }.bind(this), 1200);
		setTimeout(function() { this.setState({next: 5}); }.bind(this), 1600);
		let prev = this.state.data[0];
		if ( prev >=  this.state.alldata.length) {
			prev = 0;
		}
		this.props.setCrouselNum(prev);
		setTimeout(function() { this.setState({data: this.state.alldata[prev]}); }.bind(this), 2000);
		setTimeout(function() { this.setState({next: 4}); }.bind(this), 2000);
		setTimeout(function() { this.setState({next: 3}); }.bind(this), 2400);
		setTimeout(function() { this.setState({next: 2}); }.bind(this), 2800);
		setTimeout(function() { this.setState({next: 1}); }.bind(this), 3200);
		setTimeout(function() { this.setState({next: 0}); }.bind(this), 3600);
		setTimeout(function() { this.setState({removeTransition: false}); }.bind(this), 3600);
	}
	prev() {
		this.setState({next: 1});
		this.setState({removeTransition: true});
		setTimeout(function() { this.setState({next: 2}); }.bind(this), 400);
		setTimeout(function() { this.setState({next: 3}); }.bind(this), 800);
		setTimeout(function() { this.setState({next: 4}); }.bind(this), 1200);
		setTimeout(function() { this.setState({next: 5}); }.bind(this), 1600);
		let prev = this.state.data[0] - 2;
		if ( prev < 0) {
			prev = this.state.alldata.length - 1;
		}
		this.props.setCrouselNum(prev);		
		setTimeout(function() { this.setState({data: this.state.alldata[prev]}); }.bind(this), 2000);
		setTimeout(function() { this.setState({next: 4}); }.bind(this), 2000);
		setTimeout(function() { this.setState({next: 3}); }.bind(this), 2400);
		setTimeout(function() { this.setState({next: 2}); }.bind(this), 2800);
		setTimeout(function() { this.setState({next: 1}); }.bind(this), 3200);
		setTimeout(function() { this.setState({next: 0}); }.bind(this), 3600);
		setTimeout(function() { this.setState({removeTransition: false}); }.bind(this), 3600);
	}
  	render() {
  		var letters = this.state.data[1].split(" ");
	    return (
		        <div className={`crousel ${this.props.show ? 'show' : ''} ${this.state.fullMode ? 'full' : ''}`}>
		        	{
		        		this.state.fullMode ?
		        		(null) : (
		        			<Nav openWork={this.props.openWork} reset={this.props.reset} openAbout={this.props.openAbout} showCrousel={false} />
		        			)
		        	}

		        	<div className={`divide ${this.state.data[0] % 2 === 0 ? 'left' : ''} ${(this.state.next > 4) ? 'hide' : ''}`} style={{background : this.state.data[4]}}></div>
		        	<div className="crousel-main">

		        		
		        		
		        		{
		        			(this.state.next > 1) ?
		        			(
		        				<div className={`transition-img`}>
					        		<div className={`cover-img left ${ this.state.next > 2  ? 'clip-top' : 0}`}>
					        			<img src={this.state.data[3]} alt="" />
					        		</div>
					        		<div className={`cover-img right ${ this.state.next > 3  ? 'clip-bot' : 0}`}>
					     				<img src={this.state.data[3]} alt="" />
					        		</div>
					        	</div>
		        				):
		        			(
		        				<div className={`cover-img ${this.state.fullMode ? 'full' : ''} ${this.state.removeTransition ? 'remove-trans' : ''}`} onClick={this.setFull}>			        				
				        			<img src={this.state.data[3]} alt="" className={`${this.state.removeTransition ? 'remove-trans' : ''}`} style={{ top: this.state.imgTop + '%'}}/>

				        			<div className={`cover-txt ${(this.state.next > 0) ? 'hide' : ''} ${this.state.removeTransition ? 'remove-trans' : ''}`} style={{ top: this.state.headTop + '%'}}>
							       		<h2>
							       			{
							       				letters.map(
							       					(letter, index) => {
							       						return (
							       							<span key={index}>{letter}</span>
							       						)
							       					}
							       				)
							       			}
							       		</h2>
							       		<h6>{this.state.data[2]}</h6>
							       	</div>

							       	<div className="open-cover">
							       		<span className="one"></span>
							       		<span className="two"></span>
							       	</div>
				        		</div>

		        				)
		        		}

		        		<div className={`cover-txt ${(this.state.next > 0) ? 'hide' : ''}`}>
				       		<h2>
				       			{
				       				letters.map(
				       					(letter, index) => {
				       						return (
				       							<span key={index}>{letter}</span>
				       						)
				       					}
				       				)
				       			}
				       		</h2>
				       		<h6>{this.state.data[2]}</h6>
				       	</div>

						{
							this.state.fullMode ?
							(	
								<div className={`scroll-foot ${this.state.fullMode ? '' : 'hide'}`}>
									<div className="scroll">
										<p>Scroll to see work</p>
										<div className="scroll-line"></div>
									</div>
								</div>):
							(
								<div className={(this.state.next > 0) ? 'd-none' : ''}>
									<div className="crousel-btns next" onClick={this.next}><span>Next</span></div>
									<div className="crousel-btns prev" onClick={this.prev}><span>Prev</span></div>
								</div>)
						}
								
		        	</div>

		        	<div className={`close ${this.state.fullMode ? 'show' : ''}`} onClick={this.removeFull}></div>
		        	
				    <h5 className={`project-number ${this.state.fullMode ? 'd-none' : ''}`}>0{this.state.data[0]}/04</h5>

		        	{
		        		this.state.fullMode ? 
		        		(
		        			<ProjectContainer dataNo={this.state.data[0]} />
		        			) : (null)
		        	}
		       	</div>
	    );
  	}
}


