import React, { Component } from 'react';
// import logo from './assets/images/logo.svg';
import Hero from './components/Hero';
import Work from './components/Work';
import Screen from './components/Screen';
import Crousel from './components/Crousel';

class App extends Component {
	constructor() {
		super();
		this.state = {
			zIndexWork: false,
			showWork: false,
			menuCount: 0,
			hover: false,
			screen: '',
			showCrousel: false,
			crouselNum: 0,
			showAbout: false,
			loading: true,
		}

		this.openWork = this.openWork.bind(this);
		this.openAbout = this.openAbout.bind(this);
		this.closeWork = this.closeWork.bind(this);
		this.closeAbout = this.closeAbout.bind(this);
		this.handleScroll = this.handleScroll.bind(this);
		this.clickOnScroll = this.clickOnScroll.bind(this);
		this.reset = this.reset.bind(this);
		this.crouselThroughWork = this.crouselThroughWork.bind(this);
	}
	componentDidMount() {
		window.addEventListener('wheel', this.handleScroll);
		this.setState({loading : false});
		// setTimeout(function() { this.setState({loading: false}); }.bind(this), 5000);
	}
	componentWillUnmount() {
		window.removeEventListener('wheel', this.handleScroll);
	}
	reset() {
		this.setState({screen : 'show'});
		setTimeout(function() { this.setState({screen: 'hide'}); }.bind(this), 1200);
		setTimeout(function() { 
			this.setState({showCrousel: 'false'});
			this.setState({zIndexWork: false});
			this.setState({showWork: false});
			this.setState({menuCount: 0});
			this.setState({hover: false});
			this.setState({showCrousel: false});
		}.bind(this), 1000);
		setTimeout(function() { this.setState({screen: ''}); }.bind(this), 2200);
		window.addEventListener('wheel', this.handleScroll);
	}
	openWork() {
		this.setState({zIndexWork : true})
		this.setState({showWork : true})
		setTimeout(function() { this.setState({menuCount: 1}); }.bind(this), 300);
		setTimeout(function() { this.setState({menuCount: 2}); }.bind(this), 450);
		setTimeout(function() { this.setState({menuCount: 3}); }.bind(this), 600);
		setTimeout(function() { this.setState({menuCount: 4}); }.bind(this), 750);
	}
	closeWork() {
		this.setState({showWork : false})
		setTimeout(function() { this.setState({zIndexWork: false}); }.bind(this), 1000);
		setTimeout(function() { this.setState({menuCount: 3}); }.bind(this), 150);
		setTimeout(function() { this.setState({menuCount: 2}); }.bind(this), 300);
		setTimeout(function() { this.setState({menuCount: 1}); }.bind(this), 450);
		setTimeout(function() { this.setState({menuCount: 0}); }.bind(this), 600);
	}
	handleScroll(e) {
		e.preventDefault();
		if (e.wheelDelta > 10 || e.wheelDelta < -10) { 
			this.clickOnScroll();
		}
	}
	clickOnScroll() {
		// console.log('open');
		this.setState({screen : 'show'});
		setTimeout(function() { this.setState({screen: 'hide'}); }.bind(this), 1200);
		setTimeout(function() { this.setState({showCrousel: true}); }.bind(this), 1000);
		setTimeout(function() { this.setState({screen: ''}); }.bind(this), 2200);
		window.removeEventListener('wheel', this.handleScroll);
	}
	crouselThroughWork(num) {
		this.closeWork();

		this.setState({crouselNum : num});
		setTimeout(function() {console.log(this.state.crouselNum);}.bind(this), 1200);
		this.setState({screen : 'show'});
		setTimeout(function() { this.setState({screen: 'hide'}); }.bind(this), 1200);
		setTimeout(function() { this.setState({showCrousel: 'true'}); }.bind(this), 1000);
		setTimeout(function() { this.setState({screen: ''}); }.bind(this), 2200);
		window.removeEventListener('wheel', this.handleScroll);
	}
	openAbout() {
		this.setState({showAbout : true});
		this.reset();
		window.removeEventListener('wheel', this.handleScroll);
	}
	closeAbout() {
		this.setState({showAbout : false});
		window.addEventListener('wheel', this.handleScroll);
	}

	render() {
	    return (
	      <div className="App">
	      	{
	    		this.state.loading ?
	    		<div className="loading">
	    			<div class="loader" id="loader-2">
			          <span></span>
			          <span></span>
			          <span></span>
			        </div>
	    		</div> :
	    		<div className="wrapper--page" id="mainContainer">
		        	<Hero
		        		loading={this.state.loading}
		        		openWork={this.openWork}
		        		openAbout={this.openAbout}
		        		showAbout={this.state.showAbout}
		        		clickOnScroll={this.clickOnScroll}
		        		closeAbout={this.closeAbout}
		        		/>
		        	<Work
		        		showWork={this.state.showWork}
		        		closeWork={this.closeWork}
		        		zIndexWork={this.state.zIndexWork}
		        		menuCount={this.state.menuCount}
		        		crouselThroughWork={this.crouselThroughWork}
		        		crouselNum={this.state.crouselNum}
		        		/>
		        	<Screen screen={this.state.screen} />
		        	<Crousel
		        		reset={this.reset}
		        		openWork={this.openWork}
		        		openAbout={this.openAbout}
		        		show={this.state.showCrousel}
		        		crouselNum={this.state.crouselNum}
		        		setCrouselNum={(value) => this.setState({crouselNum : value})}
		        		/>
		        </div>
	    	}
	      </div>
	    );
	}
}

export default App;



