import React, { Component } from 'react';
import logo from './../assets/images/logo.svg';

export default class Nav extends Component {
  	render() {
	    return (
		        
			    <nav className={`navbar navbar-expand-lg ${this.props.showCrousel ? 'light' : ''}`}>
						  <div className="navbar-brand" href="#" onClick={this.props.reset}>
						  	<img src={logo} alt="" style={{height: '25px'}} />
						  </div>
						  <div className="navbar-collapse justify-content-end" id="navbarNav">
						    <ul className="navbar-nav">
						      <li className="nav-item px-3 link" onClick={this.props.openWork}>
						        <div className="nav-link">Work</div>
						      </li>
						      <li className="nav-item px-3" onClick={this.props.openAbout}>
						        <div className="nav-link">About</div>
						      </li>
						    </ul>
						  </div>
				</nav>
	    );
  	}
}


