import React, { Component } from 'react';
import ProjectImage from './ProjectImage';

export default class ProjectContainer extends Component {
	constructor() {
		super();
		this.state = {
			color: '',
			allcolor : [
				'#FE8E64',
				'#157A6A',
				'#552884',
				'#08789A',
			],
			info: [
				'Spectrum Web Application is a AWS service monitoring tool build on AWS Cloudwatch. One can enter AWS service credential to add the service to Spectrum and Start monitoring it in Real Time.',
				'My duties were Designing & Frontend development of the entire web application.',
			],
			images: [require('../../assets/images/spectrum-port-1.svg'), require('../../assets/images/spectrum-port-2.png') ],
			allImages: [
				[require('../../assets/images/medtrail-port-1.png'), require('../../assets/images/medtrail-port-2.svg'), require('../../assets/images/medtrail-port-3.svg'), require('../../assets/images/medtrail-port-4.svg'), require('../../assets/images/medtrail-port-5.svg'), require('../../assets/images/medtrail-port-6.svg')],
				[require('../../assets/images/spectrum-port-1.svg'), require('../../assets/images/spectrum-port-2.svg'), require('../../assets/images/spectrum-port-3.svg'), require('../../assets/images/spectrum-port-4.svg')],
				[require('../../assets/images/woobly-port-1.png')],
				[require('../../assets/images/excuser-port-1.png')],
			],
			allInfo: [
				[
					'Medtrail is an Application for Doctors, that helps Doctors to maintain digital records of their patients and give automatic reminders and digitized prescription to their patients',
					'I have Designed this application',
				],
				[
					'Spectrum Web Application is a AWS service monitoring tool build on AWS Cloudwatch. One can enter AWS service credential to add the service to Spectrum and Start monitoring it in Real Time.',
					'My duties were Designing & Frontend development of the entire web application.',
				],
				[
					'Woobly is a startup for enhancing the hangout experience for their users.',
					'I have designed & developed their landing page for the initial showcase of their existence.',
				],
				[
					'Excuser was a small application for promoting the company to get more projects for clients.',
					'I have designed & developed their landing page.',
				],
			],
			logo: '',
			alllogos: [require('../../assets/images/medtrail-logo.svg'), require('../../assets/images/spectrum-logo.svg'), require('../../assets/images/woobly-logo.svg'), require('../../assets/images/excuser-logo.svg') ],
			
		}
	}
	componentDidMount() {
		this.setState({color: this.state.allcolor[this.props.dataNo - 1]});
		this.setState({images: this.state.allImages[this.props.dataNo - 1]});
		this.setState({logo: this.state.alllogos[this.props.dataNo - 1]});
		this.setState({info: this.state.allInfo[this.props.dataNo - 1]});
	}
  	render() {
	    return (
			<div id="scroll" className="project-info">
				<div className="project-brief">
					<h5>PROJECT BRIEF</h5>
					{
						this.state.info.map(
			                (info,index) => {
			                    return (
			                        <p>{info}</p>
			                    )    
			                }
			            )
			        }
					{/*<p><b>Spectrum Web Application</b> is a AWS service monitoring tool build on AWS Cloudwatch. One can enter AWS service credential to add the service to Spectrum and Start monitoring it in Real Time.</p>
					<p>My duties were <b>Designing & Frontend development</b> of the entire web application.</p>*/}
				</div>
				{/*<div className="project-portfolio" style={{background: '#157A6A'}}>*/}
				<div className="project-portfolio" style={{background: this.state.color}}>
					<div className="container">
						<img src={this.state.logo} alt="" style={{height: '4rem', marginBottom: '8rem'}}/>
						
						{
							this.state.images.map(
			                    (images,index) => {
			                        return (
			                            <ProjectImage src={images} num={'0'+index} />
			                        )    
			                    }
			                )
			            }

					</div>
				</div>
			</div>    
	    );
  	}
}


