import React, { Component } from 'react';

export default class Work extends Component {
    render() {
        var works = [
            ['Medtrail','Application','#E77060','medtrail-cover'],
            ['Spectrum','Application','#24C9AF','spectrum-cover'],
            ['Woobly','Landing Page','#552884','woobly-cover'],
            ['Excuser','Landing Page','#43C1E3','excuser-cover'],
        ];
        return (
            <div className={`work ${this.props.zIndexWork ? 'front' : ''} ${this.props.showWork ? 'show' : ''}`}>
            	<div className="close show" onClick={this.props.closeWork}></div>
                {
                    works.map(
                        (works,index) => {
                            return (
                                <WorkSample 
                                    key={index}
                                    works={works}
                                    index={index}
                                    menuCount={this.props.menuCount}
                                    crouselThroughWork={this.props.crouselThroughWork}
                                    crouselNum={this.props.crouselNum} />
                                
                            )
                            
                        }
                    )
                }
            </div>
        );
    }
}

export class WorkSample extends Component {
    constructor() {
        super();
        this.open = this.open.bind(this);
    }
    open() {
        this.props.crouselThroughWork(this.props.index);
    }

    render() {
        return (
            <div
                key={this.props.index}
                className={`work-sample ${this.props.menuCount > this.props.index ? 'show' : ''}`}
                onClick={this.open}>

                    <div className="pic" style={{background: this.props.works[2]}}>
                        <img src={require(`../assets/images/${this.props.works[3]}.png`)} alt="" />
                    </div>
                    <div className="work-sample-info">
                        <div className="hov" style={{background: this.props.works[2]}}></div>
                        <div className="work-sample-info-inner">
                            <div className="work-sample-number">{'0'+(this.props.index+1)}</div>
                            <div className="work-sample-data">
                                <h3>{this.props.works[0]}</h3>
                                <p>{this.props.works[1]}</p>
                            </div>  
                        </div>
                    </div>
            </div>
        );
    }
}


