import React, { Component } from 'react';
import Nav from './Nav';


export default class Hero extends Component {
	constructor() {
		super();
		this.state = {
			init: false,
			scale: 1,
		}
	}
	componentDidMount() {
		this.setState({init: true});
	}
  	render() {
	    return (
		        <div className={`hero d-flex flex-column ${this.props.showAbout ? 'about' : ''} ${this.state.init ? 'o-1' : 'o-0' }`}>
		        	<Nav openWork={this.props.openWork} openAbout={this.props.openAbout} showCrousel={true} />
		        	<div className={`close ${this.props.showAbout ? 'show' : ''}`} onClick={this.props.closeAbout}></div>
					<div className="intro">
						<p>Hey, I am Proksh Luthra</p>
						<h1>Product Designer
						</h1>
						<div className="container my-detail">
							I am adept at understanding product needs along with customer expectations and commit to bridging this gap through my designing skills. Looking forward to leveraging my UI/UX skills for organizations that prioritize user-experience of a product and recognizes the importance of user-centered designs
						</div>
						<div className="d-flex icons-container">
							<a href="https://www.linkedin.com/in/prokshluthra/" target="_blank" rel="noopener noreferrer">
								<img src={require('../assets/images/icons/linkedin.svg')} className="icon" alt=""/>
							</a>
							<a href="https://dribbble.com/proksh" target="_blank" rel="noopener noreferrer">
								<img src={require('../assets/images/icons/dribbble.svg')} className="icon" alt=""/>
							</a>
							<a href="https://www.behance.net/prokshh90d1" target="_blank" rel="noopener noreferrer">
								<img src={require('../assets/images/icons/behance.svg')} className="icon" alt=""/>
							</a>
							<a href="https://github.com/Proksh/" target="_blank" rel="noopener noreferrer">
								<img src={require('../assets/images/icons/github.svg')} className="icon" alt=""/>
							</a>
							<a href="mailto:prokshh@gmail.com" target="_blank" rel="noopener noreferrer">
								<img src={require('../assets/images/icons/gmail.svg')} className="icon" alt=""/>
							</a>
						</div>
					</div>
					<div className="scroll-foot white" onClick={this.props.clickOnScroll}>
						<div className="scroll">
							<p><span className="d-md-none">Click</span><span className="d-none d-md-inline-block">Scroll</span> to see work</p>
							<div className="scroll-line"></div>
						</div>
					</div>
		       	</div>
	    );
  	}
}


